import React, {PropsWithoutRef, useEffect, useRef} from 'react';
import * as styles from './mountains.module.scss';
// @ts-ignore
import Mountains1 from '../../../../svgs/mountains/mountain1.svg';
// @ts-ignore
import Mountains2 from '../../../../svgs/mountains/mountain2.svg';
// @ts-ignore
import Mountains3 from '../../../../svgs/mountains/mountain3.svg';
// @ts-ignore
import Mountains4 from '../../../../svgs/mountains/mountain4.svg';
// @ts-ignore
import Mountains5 from '../../../../svgs/mountains/mountain5.svg';
// @ts-ignore
import Mountains6 from '../../../../svgs/mountains/mountain6.svg';
// @ts-ignore
import Mountains7 from '../../../../svgs/mountains/mountain7.svg';
// @ts-ignore
import Mountains8 from '../../../../svgs/mountains/mountain8.svg';


import {useSpring, animated} from 'react-spring';


const calc = (multiplierY: number = 0, multiplierX: number = 0) => (o: any) => `translateY(${o * multiplierY * globalMultiplier}px) translateX(${o * multiplierX * globalMultiplier}px)`;
let globalMultiplier = 1;

const Mountains = ({}: PropsWithoutRef<{}>) => {
    const reference = useRef(null);
    const [{offset}, set] = useSpring(() => ({offset: 0}));
    useEffect(() => {
        const handleScroll = () => {
            // @ts-ignore
            const posY = (reference && reference.current) && reference.current.getBoundingClientRect().top || 0;
            const off = Math.min(window.pageYOffset - posY, 100);
            set({offset: off});
            console.log(off);
        };
        const handleResize = () => {
            globalMultiplier = window.innerWidth > 500 ? .015 : 0.25;
        };

        handleResize();
        window.addEventListener('scroll', handleScroll);
        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('scroll', handleScroll);
            window.addEventListener('resize', handleResize);
        };
    }, [offset]);
    return (
        <div ref={reference}>
            <animated.div className={styles.mountain} style={{transform: offset.interpolate(calc(0.7))}}>
                <Mountains4 className={styles.mountainSvg}/>
            </animated.div>
            <animated.div className={styles.mountain} style={{transform: offset.interpolate(calc(0.1))}}>
                <Mountains1 className={styles.mountainSvg}/>
            </animated.div>
            <animated.div className={styles.mountain} style={{transform: offset.interpolate(calc(.1, 0.05))}}>
                <Mountains2 className={styles.mountainSvg}/>
            </animated.div>
            <animated.div className={styles.mountain} style={{transform: offset.interpolate(calc(0.1, -0.08))}}>
                <Mountains5 className={styles.mountainSvg}/>
            </animated.div>
            <animated.div className={styles.mountain} style={{transform: offset.interpolate(calc(0.1, 0.058))}}>
                <Mountains6 className={styles.mountainSvg}/>
            </animated.div>
            <animated.div className={styles.mountain} style={{transform: offset.interpolate(calc(-0.25))}}>
                <Mountains3 className={styles.mountainSvg}/>
            </animated.div>
            <animated.div className={styles.mountain} style={{transform: offset.interpolate(calc(-0.1, 0.008))}}>
                <Mountains8 className={styles.mountainSvg}/>
            </animated.div>
            <animated.div className={styles.mountain} style={{transform: offset.interpolate(calc(-0.5, 0.008))}}>
                <Mountains7 className={styles.mountainSvg}/>
            </animated.div>
        </div>
    );
};

export default Mountains;
