import React, {PropsWithoutRef, useEffect, useRef} from 'react';
import * as styles from './clouds.module.scss';
// @ts-ignore
import Clouds1 from '../../../../svgs/clouds/clouds1.svg';
// @ts-ignore
import Clouds2 from '../../../../svgs/clouds/clouds2.svg';
// @ts-ignore
import Clouds3 from '../../../../svgs/clouds/clouds3.svg';
// @ts-ignore
import Clouds4 from '../../../../svgs/clouds/clouds4.svg';
// @ts-ignore
import Clouds5 from '../../../../svgs/clouds/clouds5.svg';
// @ts-ignore
import Clouds6 from '../../../../svgs/clouds/clouds6.svg';
// @ts-ignore
import Clouds7 from '../../../../svgs/clouds/clouds7.svg';
// @ts-ignore
import Clouds8 from '../../../../svgs/clouds/clouds8.svg';
// @ts-ignore
import Clouds9 from '../../../../svgs/clouds/clouds9.svg';
// @ts-ignore
import Clouds10 from '../../../../svgs/clouds/clouds10.svg';
// @ts-ignore
import Clouds11 from '../../../../svgs/clouds/clouds11.svg';
// @ts-ignore
import Clouds12 from '../../../../svgs/clouds/clouds12.svg';
// @ts-ignore
import Clouds13 from '../../../../svgs/clouds/clouds13.svg';


import {useSpring, animated} from 'react-spring';


const calc = (multiplierY: number = 0, multiplierX: number = 0) => (o: any) => `translateY(${o * multiplierY * globalMultiplier}px) translateX(${o * multiplierX * globalMultiplier}px)`;
let globalMultiplier = 1;

const Clouds = ({}: PropsWithoutRef<{}>) => {
        const reference = useRef(null);
        const [{offset}, set] = useSpring(() => ({offset: -50}));

        useEffect(() => {
            const handleScroll = () => {
                    // @ts-ignore
                    const posY = (reference && reference.current) && reference.current.getBoundingClientRect().top || 0;
                    const off = Math.min(window.pageYOffset - posY, 100);
                    set({offset: off});
                    console.log(off);
            };

            const handleResize = () => {
                    globalMultiplier = window.innerWidth > 500 ? .5 : 0.25;
            };
            handleResize();
            window.addEventListener('scroll', handleScroll);
            window.addEventListener('resize', handleResize);
            return () => {
                window.removeEventListener('scroll', handleScroll);
                window.addEventListener('resize', handleResize);
            };
        }, [offset]);
        return (
            <div ref={reference} className={styles.cloudContainer}>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(-0.01, 0.058))}}>
                    <Clouds6 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(0.01, 0.025))}}>
                    <Clouds1 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(0.001, 0.05))}}>
                    <Clouds2 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(0.001, 0.05))}}>
                    <Clouds3 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(-0.1, 0.05))}}>
                    <Clouds4 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(0.01, 0.08))}}>
                    <Clouds5 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(0.05, 0.008))}}>
                    <Clouds7 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(-0.05, 0.08))}}>
                    <Clouds8 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(0.05, 0.1))}}>
                    <Clouds9 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(-0.01, 0.08))}}>
                    <Clouds10 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(0.04, 0.03))}}>
                    <Clouds11 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(0.05, 0.08))}}>
                    <Clouds12 className={styles.cloudSvg}/>
                </animated.div>
                <animated.div className={styles.cloud} style={{transform: offset.interpolate(calc(-0.05, 0.15))}}>
                    <Clouds13 className={styles.cloudSvg}/>
                </animated.div>
            </div>
        );
    }
;

export default Clouds;
