import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import React from 'react';
// @ts-ignore
import Logo from '../../../svgs/logo.svg';
// @ts-ignore
import HamburgerMenu from '../../../svgs/menu.svg';
import * as styles from './header.module.scss';
import {ThemeMode, toggleMenu, toggleThemeMode, uiSelector} from '../../../state/ui';

import Menu from './menu/menu';
import { Link } from 'gatsby';
import {MdBrightness3, MdBrightness7} from 'react-icons/all';

interface HeaderProps {
  siteTitle: string;
  isLandingPage: boolean;
}



const Header = ({ siteTitle, isLandingPage }: React.PropsWithoutRef<HeaderProps>) => {

  const { menuOpen, themeMode } = useSelector(uiSelector);
  const dispatch = useDispatch();

  const toggleMenuOnClick = () => dispatch(toggleMenu());
  const toggleThemeModeOnClick = () => dispatch(toggleThemeMode());


  return <header
    className={`${styles.header} ${isLandingPage ? styles.landingPage : null}`}
  >
    <Link to="/"><Logo className={styles.logo}/></Link>
    <div className={styles.buttons}>
      <HamburgerMenu className={styles.button} onClick={toggleMenuOnClick}/>
      {themeMode === ThemeMode.LIGHT ?
          <MdBrightness3 className={`${styles.button} ${styles.buttonDark}`} onClick={toggleThemeModeOnClick}/> :
          <MdBrightness7 className={`${styles.button} ${styles.buttonDark}`} onClick={toggleThemeModeOnClick}/>
      }
    </div>
    <Menu title={siteTitle} open={menuOpen} onClose={toggleMenuOnClick}/>
  </header>;
};

Header.propTypes = {
  siteTitle: PropTypes.string,
};

Header.defaultProps = {
  siteTitle: ``,
};

export default Header;
