import React, {PropsWithoutRef, useEffect, useRef} from 'react';
import * as styles from './skyline.module.scss';
import {useSpring, animated} from 'react-spring';
// @ts-ignore
import Hotelturm from '../../../../svgs/hotelturm.svg';
// @ts-ignore
import Gaskessel from '../../../../svgs/gaskessel.svg';
// @ts-ignore
import Perlach from '../../../../svgs/perlach.svg';
// @ts-ignore
import Rathaus from '../../../../svgs/rathaus.svg';


const calc = (multiplierY: number = 0, multiplierX: number = 0) => (o: any) => `translateY(${o * multiplierY * globalMultiplier}px) translateX(${o * multiplierX * globalMultiplier}px)`;
let globalMultiplier = 1;

const Skyline = ({}: PropsWithoutRef<{}>) => {
    const reference = useRef(null);
    const [{offset}, set] = useSpring(() => ({offset: -50}));

    useEffect(() => {
        const handleScroll = () => {
            // @ts-ignore
            const posY = (reference && reference.current) && reference.current.getBoundingClientRect().top || 0;
            const off = Math.min(window.pageYOffset - posY, 100);
            set({offset: off});
        };

        const handleResize = () => {
            globalMultiplier = window && window.innerWidth > 500 ? .0015 : 0.025;
        };

        handleResize();

        window.addEventListener('scroll', handleScroll);
        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('scroll', handleScroll);
            window.addEventListener('resize', handleResize);
        };
    }, [offset]);
    return (
        <div ref={reference} className={styles.skyline}>
            <animated.div style={{transform: offset.interpolate(calc(0, 0.07))}}>
                <Hotelturm className={styles.hotelturm}/>
            </animated.div>
            <animated.div style={{transform: offset.interpolate(calc(0, 0.01))}}>
                <Gaskessel className={styles.gaskessel}/>
            </animated.div>
            <animated.div style={{transform: offset.interpolate(calc(0, 0.05))}}>
                <Perlach className={styles.perlach}/>
            </animated.div>
            <animated.div style={{transform: offset.interpolate(calc(0, -0.08))}}>
                <Rathaus className={styles.rathaus}/>
            </animated.div>
        </div>
    );
};

export default Skyline;
