import React, {PropsWithChildren} from 'react';
import useSiteMetadata from '../../SiteMetadata';
import Header from '../../components/header/header';
import Footer from '../../components/footer/footer';
import {Image, PreviewImage} from '../../../models/Image';
import Hero from '../../components/hero/hero';

import './layout.css';
import '../../../styles/styles.scss';
import * as styles from './layout.module.scss';
import PreviewCompatibleImage from '../../PreviewCompatibleImage';


const Layout = ({children, imageClass = '', heroImage = false, image}: PropsWithChildren<{ heroImage?: boolean; imageClass?: string, image?: Image | string | undefined }>) => {
    const {title, description} = useSiteMetadata();
    const imageInfo: PreviewImage = {image};
    const imageClasses: {[key: string]: string} = {
        fullWidth: styles.headerImageFullWidth,
        about: styles.headerImageAbout
    };
    const classChosen = imageClasses[imageClass] || styles.headerImageFullWidth;
    return (
        <div>
            <Header isLandingPage={heroImage && !!image} siteTitle={title}/>
            {heroImage ? (!!image ? <PreviewCompatibleImage imageClass={classChosen} imageInfo={imageInfo}/> : <Hero title={title}/> ): null}
            <main className={styles.mainContent}>{children}</main>
            <Footer/>
        </div>
    );
};

export default Layout;
