import React, {PropsWithoutRef, useEffect, useRef, useState} from 'react';
import * as styles from './moon-sun.module.scss';
import {useSpring, animated} from 'react-spring';
import {MdBrightness3, FiSun} from 'react-icons/all';
import {useSelector} from 'react-redux';
import {ThemeMode, uiSelector} from '../../../../state/ui';


const calc = (multiplierY: number = 0, multiplierX: number = 0) => (o: any) => `translateY(${o * multiplierY * globalMultiplier}px) translateX(${o * multiplierX * globalMultiplier}px)`;
const globalMultiplier = 1;

const MoonSun = ({}: PropsWithoutRef<{}>) => {
        const [{offset}, setOffset] = useSpring(() => ({offset: 0}));
        const [{opacity}, setOpacity] = useSpring(() => ({opacity: 0}));
        const {themeMode} = useSelector(uiSelector);
        const [maxOffset, setMaxOffset] = useState(500);
        useEffect(() => {
            const handleResize = () => {
                setMaxOffset(window.innerHeight > 1000 ? 500 : 300);
            };
            handleResize();
            window.addEventListener('resize', handleResize);
            return () => window.removeEventListener('resize', handleResize);
        });
        useEffect(() => {
            setOffset({offset: (themeMode === ThemeMode.DARK) ? maxOffset : 0});
            setOpacity({opacity: (themeMode === ThemeMode.DARK) ? 1 : 0});
        }, [offset, themeMode]);
        return (
            <div className={styles.moonSun}>
                <animated.div className={styles.moon} style={{opacity, transform: offset.interpolate(calc(-1))}}>
                    <MdBrightness3 className={styles.moonSvg}/>
                </animated.div>
                <animated.div className={styles.sun}
                              style={{top: -1 *maxOffset, opacity: opacity.interpolate(o => 1 - o), transform: offset.interpolate(calc(1))}}>
                    <FiSun className={styles.sunSvg}/>
                </animated.div>
            </div>
        );
    }
;

export default MoonSun;
